@font-face {
    font-family: PermanentMarker;
    src: url(../public/fonts/PermanentMarker-Regular.ttf);
    font-weight: 500;
}

* {
    padding: 0;
    margin: 0;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

:root {
    --transition: 0.25s;
    --bg-color: #0053d08a;
    --sec-color: #e1e119;
}


.App {
    text-align: center;
    width: 100vw;
    max-width: 100vw;
    height: 100vh;
    max-height: 100vh;
    background-color: #282c34;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
}

.popup {
    width: 100vw;
    height: 100vh;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5625rem;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
    background-color: #0000009f;
    user-select: none;
}

.popup>span {
    width: 25rem;
    height: 16.25rem;
    background-color: #377492d9;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    border-radius: .625rem;
    box-shadow: 0rem 0rem .625rem #373737;
}

.popup>span>div {
    font-size: 1.5625rem !important;
    font-weight: 800;
    color: #f9f9f9;
    letter-spacing: .0625rem;
}

@media (max-width: 28.125rem) {
    .popup>span {
        width: 22.5rem;
    }
}

.btns-group {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.25rem;
}

.btns-group button {
    border-radius: .5rem;
    padding: .375rem .625rem;
    font-size: 1rem;
    font-weight: 700 !important;
    cursor: pointer;
}

header {
    background: var(--bg-color);
    padding: .3125rem;
    text-align: center;
    width: 99%;
    border-bottom: .125rem solid var(--sec-color);
}

header h1 {
    font-size: 1.5rem;
}

.score-board {
    width: 36.5625rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.25rem;
    height: 4.6875rem;
    background-color: var(--bg-color);
    border-radius: .625rem;
    font-size: 1.25rem;
    border-bottom: .125rem solid var(--sec-color);
    padding: .5rem;
}

.game-body {
    height: 100%;
    width: 100%;
    margin: .625rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.x-score,
.o-score {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: .625rem;
    font-size: 1.625rem;
    letter-spacing: .0625rem;
    font-weight: 800;
}

.x-score>span,
.o-score>span {
    background-color: #006eff;
    width: 1.875rem;
    border-radius: .1875rem;
    aspect-ratio: 1 /1;
    font-weight: 900;
    border-bottom: .125rem solid var(--sec-color);
    color: yellow;
}

.container {
    position: relative;
    height: 37.5rem;
    background: #0052d0;
    margin: .625rem auto;
    border-radius: .625rem;
    display: grid !important;
    align-content: center;
    grid-template-columns: repeat(auto-fill, minmax(10.625rem, 1fr)) !important;
    gap: .625rem;
    padding: 1.5625rem;
    user-select: none;
    border-bottom: .25rem solid var(--sec-color);
}

.container>div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10.625rem;
    background: #003b93;
    font-size: 4.375rem !important;
    cursor: pointer;
    transition: var(--transition) ease-in-out;
    border-radius: .25rem;
    border-bottom: .25rem solid transparent;
    font-family: PermanentMarker;
}

.container>div:hover {
    transform: scale(1.05);
    border-color: var(--sec-color);
    color: yellow;
}

.win-line {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    width: 115%;
    max-width: 34.375rem;
    height: .625rem !important;
    border-radius: .625rem;
    background-color: lime;
    z-index: 99;
    transition: var(--transition) ease-in-out;
}

.win-line.none {
    width: 0;
}

.win-line.row0 {
    width: 85%;
    top: 18.5%;
    transform: translate(-50%, 0);
}

.win-line.row1 {
    width: 85%;
    transform: translate(-50%, -50%);
}

.win-line.row2 {
    top: 81%;
    transform: translate(-50%, -50%);
    width: 85%;
}


.win-line.col0 {
    width: 85%;
    left: 19%;
    transform: translate(-50%, -50%) rotate(90deg);
}

.win-line.col1 {
    width: 85%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(90deg);
}

.win-line.col2 {
    width: 85%;
    left: 80.5%;
    transform: translate(-50%, -50%) rotate(90deg);
}

.win-line.mainD {
    width: 115%;
    transform: translate(-50%, -50%) rotate(45deg);
}

.win-line.secD {
    width: 115%;
    transform: translate(-50%, -50%) rotate(135deg);
}

@media (max-width: 87.5rem) {
    .score-board {
        width: 31.25rem !important;
    }

    .x-score,
    .o-score {
        gap: .625rem;
        font-size: 1.25rem;
        letter-spacing: .0625rem;
    }

    .container {
        height: 31.25rem;
        width: 31.25rem !important;
        margin: .625rem auto;
        border-radius: .625rem;
        display: grid !important;
        grid-template-columns: repeat(auto-fill, minmax(8.75rem, 1fr)) !important;
        gap: .625rem;
        padding: 1.5625rem;
    }

    .container>div {
        height: 8.75rem;
    }
}

@media (max-width: 32.8125rem) {
    .score-board {
        width: 23.4375rem !important;
        height: 5.625rem;
    }

    .x-score,
    .o-score {
        gap: .625rem;
        font-size: 1.25rem;
        letter-spacing: .0625rem;
    }

    .container {
        height: 25rem;
        width: 23.4375rem !important;
        margin: .625rem auto;
        border-radius: .625rem;
        display: grid !important;
        grid-template-columns: repeat(auto-fill, minmax(6.25rem, 1fr)) !important;
        gap: .625rem;
        padding: 1.5625rem;
    }

    .container>div {
        height: 6.25rem;
        font-size: 3.4375rem;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}